/* From Uiverse.io by nihad-abbasov */
/* switch settings 👇 */
.DarkModeButton {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 1rem;
  /* actions */
}
@media (min-width: 768px) {
  .DarkModeButton {
    justify-content: flex-end;
  }
}
.DarkModeButton .ui-switch {
  /* switch */
  --switch-bg: rgb(135, 150, 165);
  --switch-width: 48px;
  --switch-height: 20px;
  /* circle */
  --circle-diameter: 32px;
  --circle-bg: rgb(232, 89, 15);
  --circle-inset: calc((var(--circle-diameter) - var(--switch-height)) / 2);
}
.DarkModeButton .ui-switch input {
  display: none;
}
.DarkModeButton .slider {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: var(--switch-width);
  height: var(--switch-height);
  background: var(--switch-bg);
  border-radius: 999px;
  position: relative;
  cursor: pointer;
}
.DarkModeButton .slider .circle {
  top: calc(var(--circle-inset) * -1);
  left: 0;
  width: var(--circle-diameter);
  height: var(--circle-diameter);
  position: absolute;
  background: var(--circle-bg);
  border-radius: inherit;
  background-image: url("data:image/svg+xml;base64,");
  background-repeat: no-repeat;
  background-position: center center;
  -webkit-transition: left 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, -webkit-transform 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  -o-transition: left 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, transform 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  transition: left 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, transform 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, -webkit-transform 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
}
.DarkModeButton .slider .circle::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.75);
  border-radius: inherit;
  -webkit-transition: all 500ms;
  -o-transition: all 500ms;
  transition: all 500ms;
  opacity: 0;
}
.DarkModeButton .ui-switch input:checked + .slider .circle {
  left: calc(100% - var(--circle-diameter));
  background-image: url("data:image/svg+xml;base64,");
  background-color: rgb(0, 56, 146);
}
.DarkModeButton .ui-switch input:active + .slider .circle::before {
  -webkit-transition: 0s;
  -o-transition: 0s;
  transition: 0s;
  opacity: 1;
  width: 0;
  height: 0;
}

html {
  scroll-behavior: smooth;
}

.header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.header .columnHeader {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .header .columnHeader {
    grid-template-columns: repeat(2, 1fr);
    justify-content: start;
  }
}
.header .columnHeader .ImageLoco {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem 1rem 2rem 1rem;
}
.header .columnHeader .ImageLoco img {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 40%;
  height: auto;
}
@media (min-width: 768px) {
  .header .columnHeader .ImageLoco {
    justify-content: start;
    align-items: start;
  }
}
.header .columnHeader .mobile-menu {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1.5rem;
  filter: invert(100%);
  cursor: pointer;
}
@media (min-width: 768px) {
  .header .columnHeader .mobile-menu {
    display: none;
  }
}
.header .columnHeader .mobile-menu img {
  width: 5rem;
  height: auto;
}
.header .columnHeader .navegacion {
  top: 16rem;
  left: 0;
  width: 100%;
  text-align: center;
  max-height: 0;
  overflow: hidden;
  opacity: 0;
  transition: max-height 1s ease, opacity 1s ease;
  z-index: 1000;
}
.header .columnHeader .navegacion a {
  display: block;
  justify-content: center;
  font-size: 1rem;
  color: #1138cc;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  text-decoration: none;
  padding: 0.1rem;
  margin-top: 0.5rem;
  transition: transform 1s;
}
.header .columnHeader .navegacion a:hover {
  transform: scale(1.1);
  color: #00131d;
}
@media (min-width: 768px) {
  .header .columnHeader .navegacion a {
    justify-content: center;
    font-size: 1.5rem;
    padding: 0.5rem;
  }
}
@media (min-width: 1200px) {
  .header .columnHeader .navegacion a {
    font-size: 2rem;
  }
}
@media (min-width: 1680px) {
  .header .columnHeader .navegacion a {
    font-size: 2.5rem;
  }
}
.header .columnHeader .navegacion.mostrar {
  max-height: 15rem;
  opacity: 1;
}
@media (min-width: 768px) {
  .header .columnHeader .navegacion {
    display: flex;
    flex-direction: row;
    justify-content: center;
    max-height: none;
    opacity: 1;
    grid-column: span 2;
  }
}

