.Projects h3 {
  text-align: center;
  color: #000000;
  font-size: 2rem;
  font-weight: bold;
  font-family: "bytebounce";
  margin: 0;
}
@media (min-width: 1200px) {
  .Projects h3 {
    font-size: 5rem;
  }
}
.Projects p {
  text-align: center;
  color: black;
  font-size: 1rem;
  font-family: Arial, Helvetica, sans-serif;
  margin: 1rem;
}
@media (min-width: 1200px) {
  .Projects p {
    font-size: 1.8rem;
    max-width: 1200px;
  }
}
.Projects .ProyectColumn {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  justify-content: center;
}
@media (min-width: 768px) {
  .Projects .ProyectColumn {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1200px) {
  .Projects .ProyectColumn {
    grid-template-columns: repeat(3, 1fr);
  }
}
.Projects .ProyectColumn .Project {
  width: 95%;
  margin: 0.5rem;
  border: rgb(90, 90, 90) solid 2px;
}
.Projects .ProyectColumn .Project .ImageContainer {
  position: relative;
  overflow: hidden;
  height: 300px;
}
.Projects .ProyectColumn .Project .ImageContainer img {
  width: 100%;
  height: 100%;
  border-bottom: rgb(90, 90, 90) solid 4px;
  transition: opacity 1s ease;
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
}
.Projects .ProyectColumn .Project .ImageContainer .HoverImage {
  opacity: 0;
}
.Projects .ProyectColumn .Project .ImageContainer:hover .MainImage {
  opacity: 0;
}
.Projects .ProyectColumn .Project .ImageContainer:hover .HoverImage {
  opacity: 1;
}
.Projects .ProyectColumn .Project p {
  text-align: center;
  color: black;
}
.Projects .ProyectColumn .Project .GridButton {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  justify-content: center;
  margin: 0.5rem;
}
.Projects .ProyectColumn .Project .GridButton div {
  margin: 0.5rem;
  padding: 0.5rem;
}
.Projects .ProyectColumn .Project .GridButton div svg {
  width: 75px;
  height: 75px;
  margin: 0 auto;
  display: block;
}
.Projects .ProyectColumn .Project .GridButton div .JsonColor {
  width: 75px;
  height: 75px;
  margin: 0 auto;
  display: block;
  fill: #000;
}
.Projects .ProyectColumn .Project .GridButton div p {
  text-align: center;
  color: #FFFFFF;
}
.Projects .ProyectColumn .Project .GridButton a {
  grid-column: span 4;
}
.Projects .ProyectColumn .Project .GridButton a .SeeProject {
  background-color: white;
  border: black solid 2px;
  border-radius: 1vh;
  color: black;
  padding: 1rem;
  transition: 1s;
  width: 100%;
  font-family: Arial, Helvetica, sans-serif;
  cursor: pointer;
}
.Projects .ProyectColumn .Project .GridButton a .SeeProject:hover {
  background-color: black;
  border: white solid 2px;
  color: white;
}

footer {
  background-color: #000000;
  width: 100%;
  height: 15rem;
}
footer .FooterIcons {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 1rem;
  padding: 0.5rem;
}
footer .FooterIcons .GitHub svg {
  width: 50px;
  height: 50px;
  fill: transparent; /* Color inicial del relleno */
  stroke: #fff; /* Color inicial de la línea */
  cursor: pointer;
  transition: fill 1s ease, stroke 1s ease; /* Aplica la transición a 'fill' y 'stroke' */
}
footer .FooterIcons .GitHub svg:hover {
  fill: #fff; /* Color de relleno al pasar el mouse */
  stroke: #fff; /* Color de la línea al pasar el mouse */
}
footer .FooterIcons .Linkedin svg {
  width: 50px;
  height: 50px;
  fill: #fff;
  cursor: pointer;
  transition: fill 1s ease, stroke 1s ease; /* Aplica la transición a 'fill' y 'stroke' */
}
footer .FooterIcons .Linkedin svg:hover {
  fill: #0077b5; /* Color de relleno al pasar el mouse */
}